import { IComponent, componentRegister } from '../core'

export class TypewriterComponent extends IComponent {
  /**  */
  public text: string
  /**
   * /
   */
  public speed: number
  /**
   * 
   */
  public printDelay: number

  public deltaTimer: number

  constructor() {
    super()
    this.text = ''
    this.speed = 10
    this.printDelay = 0
    this.deltaTimer = 0
  }

  clear() {
    this.text = ''
    this.speed = 10
    this.printDelay = 0
    this.deltaTimer = 0
  }

  toJSON() {
    const { text, speed, printDelay } = this
    return { text, speed, printDelay }
  }
}

componentRegister('TypewriterComponent')(TypewriterComponent)
