import { Entity } from './entity'
import { IComponentClass, IComponent } from './component'
import { EntityQuery } from './entity-query'
import { World } from './world'

export class EntityManager {
  private entities: Map<string, Entity> = new Map()
  private numEntities: number = 0
  private entityQuery: EntityQuery = new EntityQuery()
  private entitiesWithComponentChanged: Set<Entity> = new Set()

  constructor(private world: World) {}

  /**
   * 
   * @param alias 
   * @returns 
   */
  public createEntity(alias?: string) {
    const entity = this.initEntity(alias)
    this.pushEntity(entity)
    return entity
  }

  /**
   * 
   * @param alias 
   */
  private initEntity(alias?: string): Entity {
    const entity = Entity.create(this.world, alias)
    entity.onComponentChanged = entity => {
      this.entitiesWithComponentChanged.add(entity)
    }
    return entity
  }

  /**
   * 
   * @param entity 
   */
  private pushEntity(entity: Entity): void {
    this.numEntities++
    this.entities.set(entity.id, entity)
  }

  /**
   * 
   * @param entity 
   */
  public removeEntity(entity: Entity) {
    this.entityQuery.onEntityRemoved(entity)
    this.numEntities--
    this.entities.delete(entity.id)
    Entity.destory(entity)
  }

  /**
   * 
   * @param entity 
   * @returns true  , false  
   */
  public hasEntity(entity: Entity) {
    return this.entities.has(entity.id)
  }

  /**
   *  id 
   * @param {string} id 
   * @returns {Entity | undefined}
   */
  public getEntity(id: string): Entity | undefined {
    return this.entities.get(id)
  }

  /**  */
  public clear() {
    this.entities.forEach(entity => {
      this.removeEntity(entity)
    })
  }

  /**
   *  System 
   * @param QueryComponents 
   * @param matchType  all | any
   * @returns 
   */
  public createQuery(QueryComponents: IComponentClass<IComponent>[], matchType: string) {
    return this.entityQuery.createQuery(QueryComponents, matchType)
  }

  /**
   * 
   */
  public updateEntityQuery() {
    this.entitiesWithComponentChanged.forEach(entity => {
      this.entityQuery.onEntityComponentChanged(entity)
    })
    this.entitiesWithComponentChanged.clear()
  }

  /**
   *  json 
   * 
   * NOTE:  JSON.stringify() 
   */
  public toJSON() {
    const json: any[] = []
    this.entities.forEach(entity => {
      json.push(entity)
    })
    return json
  }

  /**
   *  json 
   */
  public fromJSON(json: any) {
    for (const token of json) {
      const entity = this.initEntity()
      entity.fromJSON(token)
      this.pushEntity(entity)
    }
  }

  /**
   * 
   * @returns 
   */
  public stats() {
    const queryStats = this.entityQuery.stats()

    const numEntities = this.numEntities
    const numQueries = queryStats.numQueries
    const queries = queryStats.queries
    return {
      numEntities,
      numQueries,
      queries,
    }
  }
}
