import { Entity } from './entity'
import { IComponent } from './component'
import { EntityManager } from './entity-manager'
import { SystemManager } from './system-manager'
import { ISystemClass, ISystem, SystemPriority } from './system'
import { now } from './utils'

export class World {
  private entityManager: EntityManager = new EntityManager(this)
  private systemManager: SystemManager = new SystemManager(this, this.entityManager)
  public singleton: Entity = Entity.singleton(this)
  private enabled: boolean = false
  private lastTimestamp: number = 0

  /**
   *  system
   * @param systemClass system 
   * @param priority system 
   * @returns this
   */
  public registerSystem<T extends ISystem>(
    systemClass: ISystemClass<T>,
    priority: number = SystemPriority.Update
  ) {
    this.systemManager.registerSystem(systemClass, priority)
    return this
  }

  /**
   * 
   * @param alias 
   * @returns 
   */
  public createEntity(alias?: string) {
    return this.entityManager.createEntity(alias)
  }

  /**
   * 
   * @param entity 
   */
  public removeEntity(entity: Entity) {
    this.entityManager.removeEntity(entity)
  }

  /**
   * 
   * @param entity 
   */
  public removeAllEntity() {
    this.entityManager.clear()
  }

  /**
   * 
   * @param entity 
   * @returns true  , false  
   */
  public hasEntity(entity: Entity) {
    return this.entityManager.hasEntity(entity)
  }

  /**
   *  id 
   * @param {string} id 
   * @returns {Entity | undefined}
   */
  public getEntity(id: string): Entity | undefined {
    return this.entityManager.getEntity(id)
  }

  /**  */
  public mandatoryUpdateEntityQuery() {
    this.entityManager.updateEntityQuery()
  }

  /**
   * 
   * @param {number} timestamp - 
   */
  public execute(timestamp: number) {
    if (typeof timestamp === 'undefined') {
      timestamp = now()
    }
    let delta = 0
    if (this.lastTimestamp !== 0) {
      delta = timestamp - this.lastTimestamp
    }
    this.lastTimestamp = timestamp
    if (this.enabled) {
      this.systemManager.execute(delta, timestamp)
    }
  }

  /**
   *  world
   */
  public start() {
    this.enabled = true
    this.lastTimestamp = 0
  }

  /**
   *  world
   */
  public stop() {
    this.enabled = false
  }

  /**
   *  world 
   */
  public clear() {
    this.entityManager.clear()
    this.singleton.removeAllComponent()
  }

  /**
   * 
   * @returns 
   */
  public serialize() {
    const token = JSON.stringify({
      entity: this.entityManager,
      singleton: this.singleton
    })
    if (process.env.NODE_ENV === 'development') {
      console.log(JSON.parse(token))
    }
    return token
  }

  /**
   * 
   * @param token 
   */
  public deserialize(token: string) {
    this.clear()
    const json = JSON.parse(token)
    if (process.env.NODE_ENV === 'development') {
      console.log(json)
    }
    this.entityManager.fromJSON(json.entity)
    this.singleton.fromJSON(json.singleton)
  }

  /**
   * 
   * @returns 
   */
  public stats() {
    const { numEntities, numQueries, queries } = this.entityManager.stats()
    const { numSystems, executeTime } = this.systemManager.stats()

    return {
      overview: {
        numEntities,
        numSystems,
        numQueries
      },
      systems: executeTime,
      queries,
      pool: {
        entity: Entity.poolStats(),
        component: IComponent.poolStats()
      }
    }
  }
}
