import { Entity } from './entity'
import { IComponentClass, IComponent } from './component'
import { queryKey } from './utils'

/**  */
export class QueryItem {
  private entities: Entity[] = []
  public key: string = null

  constructor(
    private queryComponents: IComponentClass<IComponent>[],
    private matchType: string
  ) {
    if (queryComponents.length === 0) {
      throw new Error(`Can't create a query without "components" on System.`)
    }
  }

  /**
   * 
   * @param entity 
   * @returns true -  false - 
   */
  public match(entity: Entity) {
    if (this.matchType === 'all') {
      return this.matchAll(entity)
    } else if (this.matchType === 'any') {
      return this.matchAny(entity)
    } else {
      return false
    }
  }

  /**
   * 
   * @param entity 
   * @returns true -  false - 
   */
  public matchAll(entity: Entity) {
    return entity.hasAllComponent(this.queryComponents)
  }

  /**
   * 
   * @param entity 
   * @returns true -  false - 
   */
  public matchAny(entity: Entity) {
    return entity.hasAnyComponent(this.queryComponents)
  }

  /**
   * 
   * @returns 
   */
  public getEntity() {
    return this.entities
  }

  /**
   * 
   * @param entity 
   */
  public addEntity(entity: Entity) {
    if (this.entities.indexOf(entity) === -1) {
      this.entities.push(entity)
    }
  }

  /**
   * 
   * @param entity 
   */
  public removeEntity(entity: Entity) {
    const index = this.entities.indexOf(entity)
    if (index !== -1) {
      this.entities.splice(index, 1)
    }
  }
  
  /**
   * 
   * @returns 
   */
  public stats() {
    const queryComponents = this.queryComponents.map(componentClass => componentClass._id)
    return {
      matchType: this.matchType,
      numQueryComponents: this.queryComponents.length,
      numEntities: this.entities.length,
      queryComponents
    }
  }
}

/**  */
export class EntityQuery {
  private queries: Map<string, QueryItem> = new Map()

  /**
   * 
   * @param queryComponents 
   * @param matchType  all | any
   * @returns 
   */
  public createQuery(queryComponents: IComponentClass<IComponent>[], matchType: string) {
    const key = matchType + queryKey(queryComponents)
    //  system 
    if (this.queries.has(key)) {
      return this.queries.get(key)
    } else {
      const query = new QueryItem(queryComponents, matchType)
      query.key = key
      this.queries.set(key, query)
      return query
    }
  }

  /**
   * 
   * @param entity 
   */
  public onEntityRemoved(entity: Entity) {
    this.queries.forEach(query => {
      query.removeEntity(entity)
    })
  }

  /**
   * 
   * @param entity 
   */
  public onEntityComponentChanged(entity: Entity) {
    this.queries.forEach(query => {
      if (query.match(entity)) {
        query.addEntity(entity)
      } else {
        query.removeEntity(entity)
      }
    })
  }

  /**
   * 
   * @returns 
   */
  public stats() {
    const queries = [] as any[]
    this.queries.forEach(query => {
      queries.push(query.stats())
    })
    const numQueries = queries.length
    
    return {
      numQueries,
      queries
    }
  }
}