import { InputComponent } from '../../../components/singleton'

function wait(delay) {
  let timer = 0
  function process(world, delta) {
    if (timer >= delay) {
      return true
    } else {
      timer += delta
      return false
    }
  }

  return { process }
}

function waitClick() {
  function process(world) {
    const input = world.singleton.getComponent(InputComponent)

    if (input.isClick === false) {
      return false
    } else {
      return true
    }
  }

  return {
    process
  }
}

function goto(chapter, node) {
  function process() {
    return true
  }

  function goto() {
    return {
      chapter,
      node
    }
  }

  return {
    process,
    goto
  }
}

export default { wait, waitClick, goto }