import { Signal } from 'signals'
import { ObjectPool } from './object-pool'
import { Entity } from './entity'

export type IComponentProps = {
  [propName: string]: any
}

export type IComponentClass<T extends IComponent> = {
  new(): T
  onComponentCreated: Signal
  onComponentDestroyed: Signal
  _id: string
  _isRegister: boolean
  _isComponent: boolean
  _componentClassMap: Map<string, IComponentClass<IComponent>>
  _pools: Map<string, ObjectPool<IComponent>>
  create(
    entity: Entity,
    componentClass: IComponentClass<IComponent>,
    values?: IComponentProps
  ): IComponent
  destory(componentClass: IComponentClass<IComponent>, component: IComponent): void
}

/**
 * 
 * @param id  id
 * @returns 
 */
export function componentRegister(id: string) {
  return function (target: IComponentClass<IComponent>) {
    if (IComponent._componentClassMap.has(id)) {
      console.warn(
        `Component id: '${id}' already registered.`
      )
    } else {
      target._id = id
      target._isRegister = true
      IComponent._componentClassMap.set(id, target)
      // 
      if (!IComponent._pools.has(id)) {
        IComponent._pools.set(id, ObjectPool.of(target))
      }
    }
    return target
  }
}

/**  */
export class IComponent {
  /**  */
  public static readonly onComponentCreated: Signal = new Signal()
  /**  */
  public static readonly onComponentDestroyed: Signal = new Signal()

  public static _id: string
  public static _isRegister: boolean = false
  public static _isComponent: boolean = true
  public static _componentClassMap: Map<string, IComponentClass<IComponent>> = new Map()
  public static _pools: Map<string, ObjectPool<IComponent>> = new Map()
  private entity?: Entity = null

  /**
   * 
   * @param entity 
   * @param componentClass 
   * @returns 
   */
  public static create(
    entity: Entity,
    componentClass: IComponentClass<IComponent>,
    values?: IComponentProps
  ) {
    // const component = new componentClass()
    const pool = this._pools.get(componentClass._id)
    const component = pool.acquire()
    component.entity = entity
    if (values) {
      component._setProps(values)
    }
    component.init()
    this.onComponentCreated.dispatch({
      componentClass,
      component,
      entity
    })
    return component
  }

  /**
   * 
   * @param componentClass 
   * @param component 
   */
  public static destory(componentClass: IComponentClass<IComponent>, component: IComponent) {
    const pool = this._pools.get(componentClass._id)

    this.onComponentDestroyed.dispatch({
      componentClass,
      component,
      entity: component.entity
    })
    component.clear()
    component.entity = null
    pool.release(component)
  }

  /**
   * 
   */
  public static poolStats() {
    const components = {} as any
    this._pools.forEach((pool, id) => {
      components[id] = {
        size: pool.totalSize(),
        free: pool.totalFree(),
        used: pool.totalUsed()
      }
    })
    return components
  }

  /**  */
  public init() {}
  /**  */
  public clear() {}

  /**
   * 
   * @param values 
   */
  public _setProps(values: IComponentProps) {
    for (const key in values) {
      if (typeof values[key] !== 'undefined') {
        (this as IComponentProps)[key] = values[key]
      }
    }
  }

  /**
   *  json 
   * 
   * NOTE:  JSON.stringify() 
   * @returns  json 
   */
  public toJSON(): any {
    // 
    return {}
  }

  /**
   *  json 
   * @param json json 
   */
  // public fromJSON(json: any) {
  //   if (Object.prototype.toString.call(json) === '[object Object]') {
  //     // 
  //     this._setProps(json)
  //   }
  // }
}
