/**  */
export class ObjectPool<T> {
  private static pools: WeakMap<
    { new (...args: any[]): any },
    ObjectPool<any>
    > = new WeakMap()
  // 
  private freed: T[] = []
  // 
  private capacity: number = 100
  // 
  private size: number = 0

  /**
   * 
   * @param ObjectClass 
   * @returns 
   */
  public static of<T>(ObjectClass: { new (...args: any[]): T }) {
    let pool = this.pools.get(ObjectClass)
    if (pool === undefined) {
      this.pools.set(ObjectClass, (pool = new ObjectPool<T>(ObjectClass)))
    }
    return pool as ObjectPool<T>
  }

  /**
   * 
   * @param ObjectClass 
   */
  public static free<T>(ObjectClass: { new (...args: any[]): T }) {
    const pool = this.pools.get(ObjectClass)
    if (pool) {
      pool.clear()
      this.pools.delete(ObjectClass)
    }
  }

  /**
   * 
   * @param ObjectClass 
   */
  constructor(private ObjectClass: { new (...args: any[]): T }) {}

  /**
   * 
   * @returns 
   */
  public acquire() {
    if (this.freed.length <= 0) {
      this.expand()
    }

    const item = this.freed.pop()

    return item as T
  }

  /**
   * 
   * @param item 
   */
  public release(item: T) {
    this.freed.push(item)
  }

  /**
   * 
   */
  public clear() {
    this.freed.length = 0
  }

  /**
   * 
   */
  public expand() {
    const count = Math.round(this.capacity * 0.2)
    for (let i = 0; i < count; i++) {
      const item = new this.ObjectClass()
      this.freed.push(item)
    }
    this.size += count
    if (this.size >= this.capacity) {
      this.capacity += this.size
    }
  }

  /**
   * 
   * @returns 
   */
  public totalSize() {
    return this.size
  }

  /**
   * 
   * @returns 
   */
  public totalFree() {
    return this.freed.length
  }

  /**
   * 
   * @returns 
   */
  public totalUsed() {
    return this.size - this.freed.length
  }
}
