import {
  SpriteComponent,
  AnimationComponent,
  TransformComponent
} from '../../../../engine/components'
import { CharacterComponent } from '../../../components/tag'
import { GroupMap } from '../util'

export const group = new GroupMap('character')

export function createComponent(world, component) {
  const entity = world.createEntity()
    .addComponent(CharacterComponent)
    .addComponent(TransformComponent, {
      x: component.x,
      y: component.y
    })

  if (Array.isArray(component.res)) {
    entity.addComponent(AnimationComponent, {
      layer: `UserLayer${component.layer + 2}`,
      texturesId: component.res,
      cycle: 5000,
      animationSpeed: 0.2
    })
  } else {
    entity.addComponent(SpriteComponent, {
      layer: `UserLayer${component.layer + 2}`,
      textureId: component.res
    })
  }

  return entity
}
