import { Scene } from '../../../engine/render'
import {
  PIXIRenderSystem,
  AnimationSystem,
  TypewriterSystem,
  TextButtonSystem,
  TransformSystem,
  TextUpdateSystem,
  SoundSystem
} from '../../../engine/systems'
import {
  InputSystem,
  StorySystem,
  RemoveSystem
} from '../../systems'
import {
  InputComponent,
  // StoryComponent
} from '../../components/singleton'

import story from '../../story'

export class Main {
  constructor(game) {
    this.game = game
    this.world = null
    this.view = null

    this.systemData = null
    this.storyData = null

    this.init()
  }

  init() {
    this._initData()
    this._initWorldModule()
    this.world.start()
  }

  saveArchive() {
    return this.world.serialize()
  }

  loadArchive(data) {
    this.world.deserialize(data)
  }

  _initData() {
    this.systemData = {
      entities: {},
      chapter: 'chapter1',
      initialed: false,
      nodeIndex: 0,
      subIndex: 0
    }
    this.storyData = {}
  }

  _initWorldModule() {
    this.world = new Scene()
    this.world.interaction = this.game.renderer.plugins.interaction
    this.world.storyScript = story
    this.world.storyData = this.storyData
    this.world.systemData = this.systemData
    this.view = this.world.view
    window.world = this.world

    this.world
      .registerSystem(InputSystem)
      .registerSystem(StorySystem)
      .registerSystem(TypewriterSystem)
      .registerSystem(TextButtonSystem)
      .registerSystem(AnimationSystem)
      .registerSystem(TransformSystem)
      .registerSystem(TextUpdateSystem)
      .registerSystem(SoundSystem)
      .registerSystem(RemoveSystem)
      .registerSystem(PIXIRenderSystem)

    this.world.singleton
      .addComponent(InputComponent)
      // .addComponent(StoryComponent, {
      //   curChapter: 'chapter1',
      // })
  }

  update(delta) {
    this.world.execute(delta)
  }

  stats(stats) {
    stats.entity.update(this.world.stats().overview.numEntities, 30)
  }
}
