import Rabbit from 'crypto-js/rabbit'
import utf8 from 'crypto-js/enc-utf8'

/**
 *  localStorage
 * @param storageKey localStorage 
 * @param data 
 */
export function saveToLocalStorage(storageKey: string, data: string) {
  localStorage.setItem(storageKey, data)
}

/**
 *  localStorage 
 * @param storageKey localStorage 
 * @param mandatory 
 */
export function loadFromLocalStorage(storageKey: string, mandatory: boolean = false) {
  const data = localStorage.getItem(storageKey)
  if (!data && mandatory === false) {
    console.warn(`No data with the key value "${storageKey}" in localStorage.`)
  }
  return data
}

/**
 *  localStorage 
 * @param storageKey localStorage 
 */
export function removeFromLocalStorage(storageKey: string) {
  localStorage.removeItem(storageKey)
}

/**
 * time33 
 * @param text 
 */
export function time33(text: string) {
  for (var i = 0, len = text.length, hash = 5381; i < len; ++i) {
    hash += (hash << 5) + text.charAt(i).charCodeAt(0)
  }
  return hash & 0x7fffffff
}

/**  */
const key = utf8.parse('JQfTTft92kQ4').toString()

/**
 *  rabbit 
 * @param text 
 */
export function encryptByRabbit(text: string) {
  return Rabbit.encrypt(text, key).toString()
}

/**
 *  rabbit 
 * @param text 
 */
export function decryptByRabbit(text: string) {
  const bytes = Rabbit.decrypt(text, key)
  const originalText = bytes.toString(utf8)
  return originalText
}
