import * as PIXI from 'pixi.js'

export type options = {
  width?: number
  height?: number
  view?: HTMLCanvasElement
  transparent?: boolean
  autoDensity?: boolean
  antialias?: boolean
  forceFXAA?: boolean
  resolution?: number
  clearBeforeRender?: boolean
  preserveDrawingBuffer?: boolean
  backgroundColor?: number
  powerPreference?: string
  context?: any
}

type Plugins = {
  [key: string]: any
}

export class Renderer {
  static readonly plugins: Plugins = {}

  private renderer: PIXI.Renderer
  public readonly plugins: Plugins

  constructor(options?: options) {
    options = {
      antialias: true, // 
      ...options
    }
    this.renderer = PIXI.autoDetectRenderer(options)
    this.plugins = {}
    for (const pluginKey in Renderer.plugins) {
      this.plugins[pluginKey] = new Renderer.plugins[pluginKey](
        this.renderer,
        options
      )
    }
    this.renderer.plugins.interaction.autoPreventDefault = false
    this.renderer.view.style.touchAction = 'auto'
  }

  static registerPlugin(id: string, pluginClass: () => any): void {
    this.plugins[id] = pluginClass
  }

  get view() {
    return this.renderer.view
  }

  public render(view: PIXI.Container) {
    this.renderer.render(view)
  }

  public resize(width: number, height: number) {
    this.renderer.autoDensity = true
    this.renderer.resize(width, height)
  }
}

