import React from 'react'
import LoveMemory from './components/love-memory'

const gameStyle = {
  position: 'absolute',
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center'
}

const style = {
  display: 'flex',
  alignItems: 'center',
  height: '100%',
  maxWidth: 480,
  maxHeight: 853,
  background: 'black'
}

function App() {
  return (
    <div style={gameStyle}>
      <div style={style}>
        <LoveMemory />
      </div>
    </div>
  )
}

export default App
