import { Texture, Container } from 'pixi.js'
import { IComponent, componentRegister } from '../core'

export class ButtonComponent extends IComponent {
  // serialize fields
  /** button  */
  public tag: string
  /** button down id */
  public textureId: string
  /**  */
  public style: any
  /**  */
  public useAnimation: boolean
  // temp fields
  /**  */
  public texture: Texture
  /**  */
  public isBind: boolean
  /**  */
  public isClick: boolean
  /**  */
  public origin: Container

  /**  */
  public clickEvent: boolean
  /**  */
  public deltaTimer: number

  constructor() {
    super()
    this.tag = null
    this.textureId = null
    this.style = {}
    this.useAnimation = true

    this.texture = null
    this.isBind = false
    this.isClick = false
    this.origin = null

    this.clickEvent = false
    this.deltaTimer = 0
  }

  public init() {
    if (this.textureId) {
      this.texture = Texture.from(this.textureId)
    }
  }

  public clear() {
    this.tag = null
    this.textureId = null
    this.style = {}
    this.useAnimation = true

    this.texture = null
    this.isBind = false
    this.isClick = false
    if (this.origin) {
      this.origin.interactive = false
      this.origin.buttonMode = false
      this.origin.removeAllListeners()
    }
    this.origin = null

    this.clickEvent = false
    this.deltaTimer = 0
  }

  public toJSON() {
    const { tag, textureId, style, useAnimation } = this
    return { tag, textureId, style, useAnimation }
  }
}

componentRegister('ButtonComponent')(ButtonComponent)
